



































































import Vue from 'vue'
const Email = require('@/assets/smtp.js/smtp').Email

export default Vue.extend({
	data() {
		return {
			message: {
				name: '',
				contact: '',
				text: '',
			},
			sending: false,
		}
	},
	methods: {
		sendMessage() {
			const { name, contact, text } = this.message

			this.sending = true
			Email.send({
				SecureToken: '99840327-2d26-4db3-9fa4-a2a79d93e6e1',
				To: 'leave.ah@gmail.com',
				From: 'sqlearn.net@gmail.com',
				Subject: '[SQ-LEARN] CONTATO SQ-LEARN',
				Body: `FROM ${name} | CONTACT ${contact} | MESSAGE ${text}`,
			}).then(
				(message: string) => {
					if (/^OK$/.test(message)) {
						const success = this.$t('about.contact.success') as string
						this.$toast.open({ message: success, type: 'success' })
						this.message = {
							...this.message,
							text: '',
						}
					} else {
						const error = this.$t('about.contact.error') as string
						this.$toast.open({ message: error, type: 'error' })
					}
					this.sending = false
				}
			)
		},
	},
})
